import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Button, Form, FormGroup, NumberInput, ProgressIndicator, ProgressStep, } from "carbon-components-react";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [EventAPI.STEPPER_STEPCHANGED, EventAPI.STEPPER_SUBMITTED];

/*
    Example: <srs-stepper initial_values={step1: 1, step2: 2} steps=[{name: "step1", label: "Step 1", description: "Step 1"}, {name: "step2", label: "Step 2", description: "Step 2"}]/>
*/
const SrsStepper = function (props, ref) {
    const [currentStep, setCurrentStep] = useState(0);
    const [values, setValues] = useState(props.initial_values);
    const [steps, setSteps] = useState(props.steps || []);

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setValues({ ...values, [name]: value });
    };

    const handleNextStep = () => {
        if (currentStep === 2) {
            props.dispatchEvent(EventAPI.STEPPER_SUBMITTED, { payload: { value: values, id: props.elementid } });
        } else {
            props.dispatchEvent(EventAPI.STEPPER_STEPCHANGED, { payload: { value: currentStep + 1, id: props.elementid } });
            setCurrentStep(currentStep + 1);
        }
    };

    const handlePrevStep = () => {
        setCurrentStep(currentStep - 1);
    };

    const getProgressSteps = () => {
        return steps.map((step, index) => (
            <ProgressStep
                key={`step-${index}`}
                label={step.label}
                description={step.description}
                complete={index < currentStep}
                current={index === currentStep}
            />
        ));
    };

    return (
        <div>
            <ProgressIndicator currentIndex={currentStep}>
                {getProgressSteps()}
            </ProgressIndicator>
            <Form>
                { steps.map((step, index) => {
                    if(index == currentStep) {
                        <FormGroup legendText={step.label}>
                            <NumberInput
                                id={"step" + currentStep + "-input"}
                                name={step.name}
                                value={values[step.name]}
                                onChange={handleInputChange}
                            >
                            </NumberInput>
                        </FormGroup>
                    }
                })}

                <div>
                    <Button kind="secondary" onClick={handlePrevStep} disabled={currentStep === 0}>
                        Previous
                    </Button>
                    <Button kind="primary" onClick={handleNextStep}>
                        {currentStep === 2 ? 'Submit' : 'Next'}
                    </Button>
                </div>
            </Form>
        </div>
    );
}

const styles = `
    
`
const props = [{
    name: "initial_values",
    defaultValue: {},
    type: "object"
}, {
    name: "steps",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsStepper), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };